.homes
  grid-column: center-start / center-end
  margin: 15rem 0
  display: grid
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr))
  grid-gap: 7rem
  @media only screen and (max-width: $bp-medium)
    margin: 7rem 0
  @media only screen and (max-width: $bp-small)
    grid-gap: 4rem

.home
  background-color: $color-grey-light-1
  display: grid
  grid-template-columns: repeat(2, 1fr)
  grid-row-gap: 3.5rem

  &__img
    width: 100%
    grid-row: 1 / 2
    grid-column: 1 / -1
    z-index: 1

  &__like
    grid-row: 1 / 2
    grid-column: 2 / 3
    height: 2.5rem
    width: 2.5rem
    margin: 1rem
    justify-self: end
    z-index: 2

    fill: $color-primary
    height: 2.5rem
    width: 2.5rem

  &__name
    grid-column: 1 / -1
    grid-row: 1 / 2

    font-family: $font-display
    font-size: 1.6rem
    font-weight: 400
    text-align: center

    padding: 1.25rem
    background-color: $color-secondary
    color: #fff

    width: 80%
    transform: translateY(50%)
    justify-self: center
    align-self: end
    z-index: 2

  &__location,
  &__rooms,
  &__area,
  &__price
    display: flex
    align-items: center

    font-size: 1.5rem
    margin-left: 2rem

    svg
      fill: $color-primary
      height: 2rem
      width: 2rem
      margin-right: 1rem

  &__location,
  &__rooms
    margin-top: 2.5rem

  &__btn
    grid-column: 1 / -1
