.footer
  background-color: $color-secondary
  grid-column: full-start / full-end
  padding: 8rem
  @media only screen and (max-width: $bp-small)
    padding: 4rem

.nav
  list-style: none
  display: grid
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr))
  grid-gap: 2rem
  align-items: center

  &__link:link,
  &__link:visited
    font-size: 1.4rem
    color: #fff
    text-decoration: none
    font-family: $font-display
    text-transform: uppercase
    text-align: center
    padding: 1.5rem
    display: block
    transition: all .3s
    // @media only screen and (max-width: $bp-small)

  &__link:hover,
  &__link:active
    background-color: rgba(#fff, .05)
    transform: translateY(-3px)

.copyright
  margin: 6rem auto 0
  font-size: 1.4rem
  color: $color-grey-light-2
  text-align: left
  max-width: 55vw
  @media only screen and (max-width: $bp-small)
    max-width: 90vw
